<template>
  <div class="login">
    <div class="login_center">
      <div class="title">管理员登录</div>
      <div class="form">
        <div class="form-item">
          <i id="logo-github" class="fa fa-github-alt"></i>
          <input placeholder="Username"
            onkeyup="value=value.replace(/[^a-z]+/g,'')"
            v-model="loginForm.userName" type="text">
        </div>
        <div class="form-item">
          <i id="logo-lock" class="fa fa-lock"></i>
          <input id="pwd-inp" placeholder="Password"
            onkeyup="value=value.replace(/[^A-Z|a-z|0-9]+/g,'')"
            v-model="loginForm.passWord" type="password" @keydown.enter="login">
            <span class="password-eye" @click="handleShow">
              <i class="fa fa-eye" v-if="showPassord"></i>
              <i class="fa fa-eye-slash" v-else></i>
            </span>
        </div>
      </div>
      <div class="login_btn" @click="login">登录</div>
      <div class="welcome">欢迎登录后台管理系统</div>
    </div>
  </div>
</template>

<script>
export default {
  name:"loginRegist",
  data(){
    return{
      loginForm:{
        userName:"",
        passWord:""
      },
      showPassord:true,
      default:{
        userName:"hefang",
        passWord:"HJ997812"
      }
    }
  },
  mounted(){
    // this.loginForm.userName = ""
    // this.loginForm.passWord = ""
  },
  methods:{
    login(){
      //校验账号密码
      if(this.loginForm.userName && this.loginForm.passWord){
        if(this.loginForm.userName === this.default.userName && this.loginForm.passWord === this.default.passWord){
          this.$router.replace("/echart") //禁止从首页返回登录页，不添加历史记录
          // this.$router.replace({path:"/carousel"}) //这种写法还会向历史栈中添加历史记录
          //存储用户名
          sessionStorage.setItem('userName',this.loginForm.userName)
        }else{
          this.$message.error("用户名或密码错误!")
        }
      }else{
        this.$message.info("请填写用户名和密码!")
      }
    },
    handleShow(){
      this.showPassord = !this.showPassord
      const inpElement = document.getElementById("pwd-inp")
      this.showPassord && inpElement.setAttribute("type","password");
      !this.showPassord && inpElement.setAttribute("type","text");

    }
  }
}
</script>

<style lang="less">
*{
  margin: 0;
  padding: 0;
}
.login{
  box-sizing: border-box;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url("../../../assets/login.jpg");
  background-size:cover;
  background-repeat:no-repeat;
  background-attachment:fixed;
  .login_center{
    text-align: center;
    width: 280px;
    height: 320px;
    background-color:rgba(0,0,0,0.6);
    border-radius: 5px;
    box-shadow: -12px 12px 5px rgba(0,0,0,0.5);
    position:relative;
    .title{
      color: #fff;
      font-size: 16px;
      margin-top: 50px;
      width: 100px;
      margin: 45px 0 30px 90px;
    }
    .form{
      width:240px;
      margin-left: 23px;
      display: flex;
      flex-direction: column;
      justify-content: start;
      .form-item{
        margin-bottom: 15px;
        width: 80%;
        padding-left: 10px;
        position: relative;
        #logo-github,#logo-lock{
          font-size: 18px;
          color: #fff;
          margin-right: 8px;
        }
        input{
          border:0 ;
          outline: none ;
          background-color:#77373700 !important;
          // -webkit-autofill { box-shadow: 0 0 0px 1000px #77373700 inset !important;}
          color:#fff;
          font-size: 14px;
          border-bottom:1px solid	#F5F5F5;
          width: 160px;
        }
        /* 解决input自动填充时背景颜色改变的问题 */
        input:-internal-autofill-previewed,
        input:-internal-autofill-selected {
            -webkit-text-fill-color: #FFFFFF !important;
            transition: background-color 5000s ease-in-out 0s !important;
        }

        .password-eye{
          position: absolute;
          top: -5px;
          right: 5px;
          font-size: 14px;
          color:#F5F5F5;
        }
      }
    }
    .login_btn{
      width: 150px;
      height: 28px;
      color: #fff;
      background-color:rgba(130, 215, 219,0.7);
      border-radius: 12px;
      line-height: 28px;
      margin: 15px auto;
      cursor: pointer;
    }
    .welcome{
      position: absolute;
      bottom: 25px;
      left: 65px;
      color: #fff;
      font-size: 14px;
    }
  }
}
</style>



